.results {
   max-width: 415px;
   width: 100%;
   min-height: 420px;
   background-color: hsl(183, 100%, 15%);
   border-radius: 15px;
   padding: 60px 40px 40px 40px;
   color: hsl(0, 0%, 100%);
   display: flex;
   flex-direction: column;
   @media (max-width: 912px) {
     max-width: 385px;
     min-height: 375px;
   }
   @media (max-width: 375px) {
     min-height: 250px;
     padding: 40px 25px 25px 25px;
   }
} 